/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.nice-number {
  display: inline-flex;
  justify-content: stretch;
}

.nice-number input {
  vertical-align: middle;
  -moz-appearance: textfield;
  box-sizing: content-box;
  margin: 0;
  text-align: center;
}

.nice-number input::-webkit-inner-spin-button,
.nice-number input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

:root {
  --container: 1190px;
  --color-dark-100: rgba(31, 32, 65, 1);
  --color-dark-75: rgba(31, 32, 65, .75);
  --color-dark-50: rgba(31, 32, 65, .5);
  --color-dark-25: rgba(31, 32, 65, .25);
  --color-dark-5: rgba(31, 32, 65, .05);
  --color-dark-1: rgba(31, 32, 65, .1);
  --color-purple: #bc9Cff;
  --color-green: #6fcf97;
  --color-gradient: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  --color-light: #fff;
  --color-error: #d82020;
  --bd-radius: 4px;
  --transit: .3s ease-in;
  --font-family: montserrat, arial, sans-serif;
  --sub-title: 700 12px var(--font-family);
  --sub-title-light: 400 12px var(--font-family);
  --text: 400 14px var(--font-family);
  --text-bold: 700 14px var(--font-family);
}

@font-face {
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.svg") format("svg"), url("../fonts/Montserrat-Regular.ttf") format("ttf");
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
}
@font-face {
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.svg") format("svg"), url("../fonts/Montserrat-Bold.ttf") format("ttf");
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.55;
  color: var(--color-dark-100);
}

h1 {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.2;
}

h2 {
  font-weight: 700;
  font-size: 19px;
  line-height: 1.25;
}

h3 {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.25;
  text-transform: uppercase;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: var(--container);
}

.template-uikt__container {
  margin: 0 auto;
  max-width: 1440px;
  background-image: url("../img/templates/template-uikit/logo-bg.svg");
  background-position: 30px 30px;
  background-repeat: no-repeat;
}

.text-field {
  display: flex;
  flex-direction: column;
  padding-top: 3px;
  width: 100%;
  gap: 5px;
}
.text-field__header {
  display: flex;
  justify-content: space-between;
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
}
.text-field__subtitle {
  position: relative;
  bottom: 3px;
  font: var(--sub-title-light);
  color: var(--color-dark-50);
}
.text-field__input {
  border: 1px solid var(--color-dark-25);
  border-radius: var(--bd-radius);
  padding: 13px 15px;
  height: 44px;
  font-size: 14px;
  line-height: 1.71;
  color: var(--color-dark-75);
}
.text-field__input::placeholder {
  color: var(--color-dark-25);
}
.text-field__input:hover, .text-field__input:focus {
  outline: none;
  border: 1px solid var(--color-dark-50);
}

.icon-text-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.icon-text-list__item {
  display: flex;
  border-bottom: 1px solid var(--color-dark-1);
  padding-bottom: 20px;
  gap: 14px;
}
.icon-text-list__img {
  width: 41px;
}
.icon-text-list__img img {
  display: block;
  margin: 0 auto;
}
.icon-text-list__title {
  padding-bottom: 5px;
  font: var(--text-bold);
}
.dropdown {
  max-width: 100%;
  transition: var(--transit);
}
.dropdown__header {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  display: flex;
  justify-content: space-between;
  padding-bottom: 4px;
}
.dropdown__subtitle {
  font: var(--sub-title-light);
  color: var(--color-dark-50);
}
.dropdown__btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  border: 1px solid var(--color-dark-25);
  border-radius: var(--bd-radius) var(--bd-radius) 0 0;
  padding: 15px;
  width: 100%;
  height: 44px;
  font: var(--text);
  line-height: 1;
  color: var(--color-dark-75);
  background-color: var(--color-light);
  cursor: pointer;
}
.dropdown__menu {
  position: relative;
}
.dropdown__list {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  position: absolute;
  left: 0;
  top: 44px;
  border: 1px solid var(--color-dark-50);
  border-top: none;
  border-radius: 0 0 var(--bd-radius) var(--bd-radius);
  padding: 5px 7px 1px 15px;
  width: 100%;
  box-shadow: 0 10px 20px 0 rgba(31, 32, 65, 0.05);
  background-color: var(--color-light);
  opacity: 0;
  visibility: hidden;
}
.dropdown__item {
  padding-bottom: 7px;
}
.dropdown__label {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.dropdown__input:disabled {
  background: transparent;
}

.dropdown--open .dropdown__btn {
  border-color: var(--color-dark-50);
}
.dropdown--open .dropdown__list {
  opacity: 1;
  visibility: visible;
}

.nice-number {
  justify-content: space-between;
  width: 92px;
}
.nice-number__button--disabed {
  border-color: var(--color-dark-25) !important;
  color: var(--color-dark-25) !important;
  cursor: not-allowed !important;
}

.nice-number button {
  border: 1px solid var(--color-dark-50);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  font-size: 18px;
  color: var(--color-dark-50);
  background-color: transparent;
  cursor: pointer;
}

.nice-number input {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  border: none;
  max-width: 20px;
}

.radio__title {
  margin-bottom: 16px;
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
}
.radio__container {
  display: flex;
  align-items: center;
  gap: 20px;
}
.radio__input {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  opacity: 0;
}
.radio__input:checked + .radio__span {
  color: var(--color-dark-75);
}
.radio__input:checked + .radio__span::after {
  opacity: 1;
}
.radio__input:checked + .radio__span::before {
  border-color: var(--color-purple);
}
.radio__span {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.2;
  color: var(--color-dark-50);
  transition: var(--transit);
}
.radio__span::before {
  content: "";
  margin-right: 10px;
  border: 1px solid var(--color-dark-25);
  border-radius: 100%;
  width: 20px;
  height: 20px;
  transition: var(--transit);
}
.radio__span::after {
  content: "";
  position: absolute;
  left: 4px;
  top: 50%;
  border-radius: 100%;
  width: 12px;
  height: 12px;
  background: var(--color-gradient);
  opacity: 0;
  transform: translateY(-50%);
  transition: var(--transit);
}

.subscr-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 4px;
}
.subscr-form__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
}
.subscr-form__wrapp {
  display: flex;
}
.subscr-form__input {
  flex-grow: 1;
  border: 1px solid var(--color-dark-25);
  border-right: none;
  border-radius: var(--bd-radius) 0 0 var(--bd-radius);
  padding: 13px;
  height: 44px;
  font: var(--text);
  line-height: 1.7;
  color: var(--color-dark-75);
}
.subscr-form__input::placeholder {
  color: var(--color-dark-25);
}
.subscr-form__input:hover, .subscr-form__input:focus, .subscr-form__input--focus {
  outline: none;
  border: 1px solid var(--color-dark-50);
}
.subscr-form__input:focus + .subscr-form__button:hover, .subscr-form__input--focus + .subscr-form__button:hover {
  border-left: none;
  width: 44px;
}
.subscr-form__button {
  position: relative;
  flex-grow: 0;
  border: 1px solid var(--color-dark-25);
  border-left: none;
  border-radius: 0 var(--bd-radius) var(--bd-radius) 0;
  width: 44px;
  background: transparent;
  background-image: url("../img/components/subscription-text-field/arrow_forward.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  cursor: pointer;
}
.subscr-form__button::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: var(--color-dark-50);
  opacity: 0;
}
.subscr-form__button:hover, .subscr-form__button:focus, .subscr-form__button--focus {
  outline: none;
  border: 1px solid var(--color-dark-50);
  border-left: none;
}
.subscr-form__button:hover.subscr-form__button::before {
  opacity: 1;
}

.subscr-form__error {
  color: var(--color-error);
}

.subscr-form__input.subscr-form__error {
  border: 1px solid var(--color-error);
  color: var(--color-dark-75);
}

.bullet-list__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  padding-bottom: 14px;
}
.bullet-list__item {
  position: relative;
  padding-bottom: 10px;
  padding-left: 22px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  color: var(--color-dark-75);
}
.bullet-list__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 7px;
  border-radius: 100%;
  width: 10px;
  height: 10px;
  background-color: var(--color-dark-25);
}

.checkbox {
  display: flex;
  line-height: 1.47;
  gap: 10px;
}
.checkbox__input {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}
.checkbox__input:checked + .checkbox__box::after {
  opacity: 1;
}
.checkbox__input:checked + .checkbox__box {
  border-color: var(--color-purple);
}
.checkbox__box {
  flex-shrink: 0;
  border: 1px solid var(--color-dark-25);
  border-radius: var(--bd-radius);
  width: 20px;
  height: 20px;
  transition: var(--transit);
}
.checkbox__box::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url("../img/components/checkbox/check_arrow.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  opacity: 0;
  transition: var(--transit);
}

.checkbox-btns__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  margin-bottom: 16px;
}
.checkbox-btns__wrap {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.rich-checkbox-btns {
  max-width: 100%;
}
.rich-checkbox-btns__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  margin-bottom: 16px;
}
.rich-checkbox-btns__item {
  margin-bottom: 9px;
}
.rich-checkbox-btns__item label {
  margin-bottom: 5px;
  font: var(--text-bold);
  color: var(--color-dark-75);
}
.rich-checkbox-btns__description {
  margin-left: 30px;
  font: var(--sub-title-light);
  line-height: 1.17;
  color: var(--color-dark-50);
}

.rate-button {
  --star-width: 20px;
  --star-offset: 4px;
  --star-count: 5;
  --star-offset-count: 4;
}
.rate-button__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  margin-bottom: 16px;
}
.rate-button__wrapp {
  display: flex;
  gap: 20px;
}
.rate-button__group {
  position: relative;
  margin: 2px;
  width: calc(var(--star-width) * var(--star-count) + var(--star-offset) * var(--star-offset-count));
  height: 20px;
  background-image: url("../img/components/rate-button/star-empty.svg");
  background-size: 20px auto;
  background-repeat: space;
}
.rate-button__star {
  appearance: none;
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  width: var(--star-width);
  height: var(--star-width);
  background-size: var(--star-width) auto;
  background-repeat: space;
  cursor: pointer;
}
.rate-button__star:checked, .rate-button__star:hover {
  background-image: url("../img/components/rate-button/star.svg");
}
.rate-button__star:hover ~ .rate-button__star {
  background-image: url("../img/components/rate-button/star-empty.svg");
}
.rate-button__star:nth-of-type(1) {
  z-index: 5;
  width: calc(var(--star-width) + var(--star-offset));
}
.rate-button__star:nth-of-type(2) {
  z-index: 4;
  width: calc(var(--star-width) * 2 + var(--star-offset));
}
.rate-button__star:nth-of-type(3) {
  z-index: 3;
  width: calc(var(--star-width) * 3 + var(--star-offset) * 2);
}
.rate-button__star:nth-of-type(4) {
  z-index: 2;
  width: calc(var(--star-width) * 4 + var(--star-offset) * 3);
  width: 92px;
}
.rate-button__star:nth-of-type(5) {
  z-index: 1;
  width: calc(var(--star-width) * var(--star-count) + var(--star-offset) * var(--star-offset-count));
}

.toggle__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  margin-bottom: 16px;
}
.toggle__item {
  margin-bottom: 14px;
}
.toggle__input {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}
.toggle__input:focus + .toggle__display {
  outline: 1px dotted #212121;
  outline: 1px auto -webkit-focus-ring-color;
}
.toggle__input__input:focus:not(:focus-visible) + .toggle__display {
  outline: none;
}
.toggle__input:checked + .toggle__display {
  border-color: var(--color-purple);
}
.toggle__input:checked + .toggle__display::before {
  left: 61%;
  background: var(--color-gradient);
}
.toggle__label {
  display: flex;
  align-items: center;
  font: var(--text);
  line-height: 0;
  color: var(--color-dark-75);
  cursor: pointer;
  gap: 10px;
}
.toggle__display {
  position: relative;
  border: 1px solid var(--color-dark-25);
  border-radius: 10px;
  width: 40px;
  height: 20px;
}
.toggle__display::before {
  content: "";
  position: absolute;
  left: 8%;
  top: 50%;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background: var(--color-dark-25);
  transform: translateY(-50%);
}

.colors-type {
  padding-top: 145px;
  padding-right: 130px;
  padding-left: 151px;
}
.colors-type__container {
  display: flex;
  justify-content: space-between;
  background-image: url("../img/logo.svg");
  background-position: 30px 30px;
  background-repeat: no-repeat;
}

.colors {
  display: grid;
  gap: 20px;
}
.colors__item {
  display: flex;
  align-items: center;
  gap: 40px;
}
.colors__color {
  border-radius: 6px;
  width: 70px;
  height: 70px;
  background: var(--color-bg);
}
.colors__title {
  line-height: 1.46;
}
.colors__value {
  font-size: 19px;
  line-height: 1.46;
  text-transform: uppercase;
}

.type {
  display: flex;
  flex-direction: column;
  min-width: 463px;
  gap: 51px;
}
.type__item *:first-child {
  display: grid;
  grid-template-columns: 38px 369px;
  gap: 29px;
}
.type__item *:first-child span {
  font-weight: 400;
  text-align: right;
  color: var(--color-dark-25);
}
.type__item *:first-child p {
  max-width: 200px;
}

.forms-elements {
  padding: 140px;
}
.forms-elements__container {
  display: grid;
  grid-template-columns: 320px 126px 320px 72px 320px;
  margin-bottom: 97px;
  height: 1330px;
}
.forms-elements__footer {
