  grid-template-columns: 320px 711px;
  gap: 167px;
}
.forms-elements__column {
  display: grid;
  align-content: start;
}
.forms-elements__column > div {
  align-self: start;
}
.forms-elements__column--left {
  grid-column: 1/2;
}
.forms-elements__column--center {
  grid-column: 3/4;
}
.forms-elements__column--right {
  grid-column: 5/6;
}
.forms-elements__text-field-one {
  margin-bottom: 17px;
}
.forms-elements__text-field-two {
  margin-bottom: 421px;
}
.forms-elements__subscription-text-field {
  margin-bottom: 100px;
  max-width: 267px;
}
.forms-elements__dropdown {
  position: relative;
  z-index: 1;
  margin-bottom: 22px;
  max-width: 266px;
}
.forms-elements__dropdown--open {
  max-width: 266px;
}
.forms-elements__checkbox-buttons {
  margin-top: 4px;
  margin-bottom: 36px;
  max-width: 222px;
}
.forms-elements__radio-buttons {
  margin-bottom: 41px;
}
.forms-elements__toggle {
  margin-bottom: 122px;
}
.forms-elements__rate-buttons {
  margin-bottom: 516px;
}
.forms-elements__rich-checkbox-buttons {
  max-width: 263px;
}
.forms-elements__bullet-list {
  margin-top: 1056px;
  max-width: 263px;
}
.forms-elements__icon-text-list {
  margin-left: 3px;
}